$primary: #650300; 
$primary-light: #AA0601;
$secondary:#FFFFFF;
$bg-color:#AA06010D;
$default-color:#000000;
$smwords-color:#707070;


$font-noto: 'Noto Sans TC', sans-serif;
$font-roboto: 'Roboto', sans-serif;


$font-size-base: 16px;
$font-size-base-sm:$font-size-base*1.25;
$font-size-base-m:$font-size-base*1.75;
$font-size-base-ml:$font-size-base*2; 
$h1-font-size: $font-size-base * 4;
$h2-font-size: $font-size-base * 3;
$h3-font-size: $font-size-base * 2.25; 
$h4-font-size: $font-size-base *1.5;


